'use client'
import { motion } from "framer-motion";
import { FiTwitter, FiLinkedin, FiFacebook, FiInstagram , FiMenu, FiX} from "react-icons/fi";
import { useState } from "next/link"; 
const Header = () => {
  // Toggler the menu button colse and open
  const [isOpen, setIsOpen] = useState(false);
  const toggleMenu = () => setIsOpen(!isOpen);
  return (
    <header className="absolute w-full z-50 transition-all duration-300">
      <div className="container mx-auto px-4 sm:px-6 lg:px-8 flex items-center justify-between  h-16 md:h-20 ">
        {/* Logo/Name */}
        <motion.div
          initial={{ opacity: 0, x: -100 }}
          animate={{ opacity: 1, x: 0 }}
          transition={{
            type: "spring",
            stiffness: 100,
            damping: 25,
            delay: 0.3,
            duration: 1.2,
          }}
          className="flex items-center"
        >
          {/* {my } */}
          <div className="h-10 w-10 rounded-3xl  bg-gradient-to-r from-yellow-500 to-yellow-700 flex items-center justify-center text-orange-700 font-bold text-xl  mr-3 ">
            M-Y
          </div>
          {/* {FullName} */}
          <span className="text-xl  font-bold bg-gradient-to-r  dark:from-gray-300 dark:to-gray-100 from-gray-900 to-gray-600 bg-clip-text text-transparent">
            FullName
          </span>
        </motion.div>
        {/* Desktop Navigation */}
        <nav className="lg:flex hidden items-center justify-center space-x-8">
          {["Home", "About", "Project", "experience", "Contact"].map(
            (item, index) => (
              <motion.a
                key={item}
                initial={{ opacity: 0, y: -20 }}
                animate={{ opacity: 1, y: 0 }}
                transition={{
                  type: "spring",
                  stiffness: 100,
                  damping: 20,
                  delay: 0.7 + index * 0.2,
                }}
                className="relative text-gray-800 dark:text-gray-200 
                hover:text-orange-700 dark:hover:text-orange-500 font-medium 
                transition-colors duration-300 group"
                href="#"
              >
                {item}
                <span className="absolute bottom-0 left-0 w-0 h-0.5 bg-yellow-500 group-hover:w-full transition-all duration-300"></span>
              </motion.a>
            )
          )}
          {/* { social icon } */}
          <div className="md:flex hidden items-center justify-center space-x-4">
            <motion.a
              initial={{ opacity: 0, scale: 0.5 }}
              animate={{ opacity: 1, scale: 1 }}
              transition={{
                delay: 1.5,
                duration: 0.8,
              }}
              className="text-gray-900 dark:text-gray-200 hover:text-orange-700 dark:hover:text-orange-500 transition-colors duration-300 "
              href="#"
            >
              <FiTwitter className="w-5 h-5" />
            </motion.a>
             <motion.a
              initial={{ opacity: 0, scale: 0.5 }}
              animate={{ opacity: 1, scale: 1 }}
              transition={{
                delay: 1.5,
                duration: 0.8,
              }}
              className="text-gray-900 dark:text-gray-200 hover:text-orange-700 dark:hover:text-orange-500 transition-colors duration-300 "
              href="#"
            >
              <FiInstagram className="w-5 h-5" />
            </motion.a>
            <motion.a
              initial={{ opacity: 0, scale: 0.5 }}
              animate={{ opacity: 1, scale: 1 }}
              transition={{
                delay: 1.5,
                duration: 0.8,
              }}
              className="text-gray-900 dark:text-gray-200 hover:text-orange-700 dark:hover:text-orange-500 transition-colors duration-300 "
              href="#"
            >
              <FiFacebook className="w-5 h-5" />
            </motion.a>
            <motion.a
              initial={{ opacity: 0, scale: 0.5 }}
              animate={{ opacity: 1, scale: 1 }}
              transition={{
                delay: 1.5,
                duration: 0.8,
              }}
              className="text-gray-900 dark:text-gray-200 hover:text-orange-700 dark:hover:text-orange-500 transition-colors duration-300 "
              href="#"
            >
              <FiLinkedin className="w-5 h-5" />
            </motion.a>
            {/* hire me button */}
            <motion.button
            initial={{ opacity: 0, scale: 0.8 }}
            animate={{ opacity: 1, scale: 1 }}
            transition={{
              delay: 1.6,
              duration: 0.8,
              type: "spring",
              stiffness: 100,
              damping: 15,           
            }}
             className="ml-4  px-4 py-2 rounded-xl bg-gradient-to-r from-yellow-400 to-yellow-700 text-orange-700 font-bold hover:bg-gradient-to-r hover:from-orange-700 hover:to-orange-500 hover:text-yellow-200 transition-all duration-500 ">
              Hire Me
            </motion.button>
             {/* mobile navigation */}
          </div>
             <div className="md:hidden flex items-center">
               <button
                onClick={toggleMenu}
                className="text-gray-200 ">
                { isOpen ? <FiX className="w-6 h-6" /> : <FiMenu className="w-6 h-6" /> }
               </button>
             </div>
        </nav>
      </div>
    </header>
  );
};

export default Header;
